.character {
    margin-bottom: 12px;
    margin-right: 12px;
    position: relative;
}

.right {
    float: right;
    width: 300px;
    border: 3px solid #73AD21;
    padding: 10px;
}

.character-wrapper {
    display: flex;
}

span.character {
    position: relative;
}

.character>img {
    width: 64px;
    height: 64px;
}

.constellation {
    position: absolute;
    text-align: center;
    top: 0;
    right: 0;
    width: 21px;
    height: 20px;
    background: rgba(0, 0, 0, .5);
    border-radius: 0 4px 0 4px;
    color: #fff;
    line-height: 20px;
    padding-left: 3px;
    font-size: 12px;
    background: transparent url(../public/static/images/constellation_bg.png) no-repeat 50%/contain;
    font-family: "Tahoma Regular", "Tahoma Bold", sans-serif !important;
}